*{
    font-family: Arial, Helvetica, sans-serif;
}

body{
    padding: 5em;
}

.contenedor-card{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid blue;
    border-radius: 10px;
    margin-bottom: 5px;
    margin-right: 5px;
    overflow: hidden;
}

.card img{
    max-height: 150px;
    max-width: 250px;
    min-width: 250px;
}

.card p{
    max-width: 160px;
}